<template>
  <div class="container">
    <div class="form">
      <el-form label-width="160px" :model="queryData" :rules="rules" :inline="true" ref="queryForm">
        <el-form-item label="数据表名称" prop="tabName">
          <el-select v-model="queryData.tabName" placeholder="请选择数据表名称" filterable @change="queryChange">
            <el-option
              v-for="item in tabList"
              :key="item.code"
              :value="item.code"
              :label="item.name"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button plain size="small" type="primary" @click="query">查询</el-button>
        </el-form-item>
      </el-form>
      <el-form label-width="160px" :model="editData" :rules="rules2" :inline="true" ref="editForm">
        <el-form-item label="维护信息标题设置:" prop="title">
          <el-input v-model="editData.title"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="data">
      <div class="table">
          <el-table style="width: 100%" :data="searchData" stripe :header-cell-style="tableHeader">
            <el-table-column prop="tabName" label="查询表名称" min-width="120">
            </el-table-column>
            <el-table-column prop="fieldName" label="查询条件字段名称" min-width="120">
              <template slot-scope="scope">
                <el-select v-model="scope.row.fieldName" placeholder="请选择字段" @change="changeField(scope.row.fieldName,scope.$index)">
                  <el-option label="使用性质" value="usePro"></el-option>
                  <el-option label="车辆种类" value="carType"></el-option>
                  <el-option label="车辆颜色" value="carColor"></el-option>
                </el-select>
              </template>
            </el-table-column>
            <el-table-column prop="searchType" label="查询方式" min-width="120">
              <template slot-scope="scope">
                <el-select v-model="scope.row.searchType" placeholder="请选择查询方式" disabled>
                  <el-option label="编辑框" value="edit"></el-option>
                  <el-option label="下拉框" value="select"></el-option>
                </el-select>
              </template>
            </el-table-column>
            <el-table-column prop="isFlag" label="代码标志" min-width="120">
              <template slot-scope="scope">
                <el-select v-model="scope.row.isFlag" placeholder="请选择代码标志" disabled>
                  <el-option label="是" value="yes"></el-option>
                  <el-option label="否" value="no"></el-option>
                </el-select>
              </template>
            </el-table-column>
            <el-table-column prop="bizType" label="代码分类名称" min-width="120">
              <template slot-scope="scope">
                <el-select v-model="scope.row.bizType" placeholder="请选择代码分类名称" disabled>
                  <el-option label="使用性质" value="usePro"></el-option>
                  <el-option label="车辆种类" value="carType"></el-option>
                </el-select>
              </template>
            </el-table-column>
            <el-table-column fixed="right" label="操作" width="150">
              <template slot-scope="scope">
                <el-tooltip class="item" effect="dark" content="删除" placement="top">
                  <el-button
                    @click.native.prevent="removeRecord(scope.row.fieldName)"
                    type="text"
                    size="small"
                    icon="iconfont icon-iconfont-shanchu"
                    style="color: #FE2B6F">
                  </el-button>
                </el-tooltip>
              </template>
            </el-table-column>
          </el-table>
      </div>
      <el-button
        :style="specialTableBtn"
        type="primary"
        icon="el-icon-plus"
        @click="add">
        新增行
      </el-button>
      <div class="table">
          <el-table style="width: 100%" :data="contentData" stripe :header-cell-style="tableHeader">
            <el-table-column prop="tabName" label="数据表名称" min-width="120">
            </el-table-column>
            <el-table-column prop="fieldCName" label="中文字段名称" min-width="120">
            </el-table-column>
            <el-table-column prop="fieldEName" label="英文字段名称" min-width="120">
            </el-table-column>
            <el-table-column prop="dataType" label="数据类型" min-width="120">
            </el-table-column>
            <el-table-column prop="isFlag" label="代码标志" min-width="120">
              <template slot-scope="scope">
                <el-select v-model="scope.row.isFlag" placeholder="请选择代码标志" disabled>
                  <el-option label="是" value="yes"></el-option>
                  <el-option label="否" value="no"></el-option>
                </el-select>
              </template>
            </el-table-column>
            <el-table-column prop="bizType" label="代码分类名称" min-width="120">
              <template slot-scope="scope">
                <el-select v-model="scope.row.bizType" placeholder="请选择代码分类名称">
                  <el-option label="使用性质" value="usePro"></el-option>
                  <el-option label="车辆种类" value="carType"></el-option>
                  <el-option label="车辆颜色" value="carColor"></el-option>
                  <el-option label="车主姓名" value="userName"></el-option>
                  <el-option label="购车时间" value="buyTime"></el-option>
                  <el-option label="是否购买交强险" value="isForce"></el-option>
                </el-select>
              </template>
            </el-table-column>
            <el-table-column prop="isEdit" label="可编辑标志" min-width="120">
              <template slot-scope="scope">
                <el-select v-model="scope.row.isEdit" placeholder="请选择可编辑标志">
                  <el-option label="是" value="yes"></el-option>
                  <el-option label="否" value="no"></el-option>
                </el-select>
              </template>
            </el-table-column>
            <el-table-column prop="editType" label="编辑方式" min-width="120">
              <template slot-scope="scope">
                <el-select v-model="scope.row.editType" placeholder="请选择编辑方式"  :disabled="scope.row.isEdit === 'no'">
                  <el-option label="编辑框" value="edit"></el-option>
                  <el-option label="下拉单选框" value="singleSelect"></el-option>
                  <el-option label="下拉单选框" value="multiSelect"></el-option>
                  <el-option label="日期选择器" value="time"></el-option>
                </el-select>
              </template>
            </el-table-column>
            <el-table-column prop="orderNo" label="展示顺序" min-width="120">
              <template slot-scope="scope">
                <el-input v-model="scope.row.orderNo" placeholder="请填写展示顺序"></el-input>
              </template>
            </el-table-column>
            <el-table-column prop="showType" label="展示方式模板" min-width="120">
              <template slot-scope="scope">
                <el-select v-model="scope.row.showType" placeholder="请选择展示方式模板">
                  <el-option label="横向批量编辑模板" value="batchEdit"></el-option>
                  <el-option label="纵向单条编辑模板" value="singleEdit"></el-option>
                </el-select>
              </template>
            </el-table-column>
          </el-table>
      </div>
    </div>
    <div class="option">
      <el-button  type="primary" size="small" plain @click="commit">发布</el-button>
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
  name: 'TableConfig',
  data () {
    return {
      tabList: [
        {code: 'carTab', name: '车辆信息表'},
        {code: 'comTab', name: '商业险信息表'}
      ],
      queryData: {
        tabName: 'carTab'
      },
      editData: {
        title: '投保车辆'
      },
      rules: {
        tabName: [{required: true, trigger: 'blur', message: '查询条件不能为空'}]
      },
      rules2: {
        title: [{required: true, trigger: 'blur', message: '数据信息标题必须设置'}]
      },
      specialTableBtn: {
        width: '100%',
        marginTop: '5px',
        border: '1px dashed #15afff',
        background: '#fff',
        color: '#15afff',
        display: 'block'
      },
      title: '',
      tabData: [
        {
          fieldName: 'usePro',
          searchType: 'select',
          isFlag: 'yes',
          bizType: 'usePro'
        },
        {
          fieldName: 'carType',
          searchType: 'select',
          isFlag: 'yes',
          bizType: 'carType'
        },
        {
          fieldName: 'carColor',
          searchType: 'edit',
          isFlag: 'yes',
          bizType: ''
        }
      ],
      searchData: [
        {
          tabName: '车辆信息表',
          fieldName: 'usePro',
          searchType: 'select',
          isFlag: 'no',
          bizType: 'usePro'
        },
        {
          tabName: '车辆信息表',
          fieldName: 'carType',
          searchType: 'select',
          isFlag: 'yes',
          bizType: 'carType'
        }
      ],
      contentData: [
        {
          tabName: '车辆信息表',
          fieldCName: '使用性质',
          fieldEName: 'usePro',
          dataType: 'varchar(100)',
          isFlag: 'no',
          bizType: 'usePro',
          isEdit: 'yes',
          editType: 'singleSelect',
          orderNo: '1',
          showType: 'batchEdit'
        },
        {
          tabName: '车辆信息表',
          fieldCName: '车辆种类',
          fieldEName: 'carType',
          dataType: 'varchar(50)',
          isFlag: 'yes',
          bizType: 'carType',
          isEdit: 'yes',
          editType: 'multiSelect',
          orderNo: '2',
          showType: 'batchEdit'
        },
        {
          tabName: '车辆信息表',
          fieldCName: '车辆颜色',
          fieldEName: 'carColor',
          dataType: 'varchar(50)',
          isFlag: 'yes',
          bizType: 'carColor',
          isEdit: 'yes',
          editType: 'edit',
          orderNo: '3',
          showType: 'batchEdit'
        },
        {
          tabName: '车辆信息表',
          fieldCName: '车主姓名',
          fieldEName: 'userName',
          dataType: 'varchar(50)',
          isFlag: 'no',
          bizType: 'userName',
          isEdit: 'no',
          editType: '',
          orderNo: '4',
          showType: 'batchEdit'
        },
        {
          tabName: '车辆信息表',
          fieldCName: '购车时间',
          fieldEName: 'buyTime',
          dataType: 'datetime2(7)',
          isFlag: 'yes',
          bizType: 'buyTime',
          isEdit: 'yes',
          editType: 'time',
          orderNo: '5',
          showType: 'batchEdit'
        },
        {
          tabName: '车辆信息表',
          fieldCName: '是否购买交强险',
          fieldEName: 'isForce',
          dataType: 'bit',
          isFlag: 'no',
          bizType: 'isForce',
          isEdit: 'yes',
          editType: 'singleSelect',
          orderNo: '6',
          showType: 'batchEdit'
        }

      ]
    }
  },
  methods: {
    queryChange (val) {
      if (val === 'carTab') {
      }
    },
    query () {
      this.$refs['queryForm'].validate(valid => {
        if (valid) {
          if (this.queryData.tabName === 'carTab') {
          }
        }
      })
    },
    changeField (val, index) {
      const data = this.tabData.find(item => item.fieldName === val)
      this.searchData[index].searchType = data.searchType
      this.searchData[index].isFlag = data.isFlag
      this.searchData[index].bizType = data.bizType
    },
    commit () {
      this.$refs['editForm'].validate(valid => {
        if (valid) {}
      })
    },
    add () {
      const newItem = {
        tabName: '车辆信息表',
        fieldName: '',
        searchType: '',
        isFlag: '',
        bizType: ''
      }
      this.searchData.push(newItem)
    },
    removeRecord (fieldName) {
      this.$confirm('确定删除此条数据记录吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const index = this.searchData.findIndex(item => item.fieldName === fieldName)
        this.searchData.splice(index, 1)
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
      }).catch(() => {
      })
    }
  },
  computed: {
    ...mapState({tableHeader: state => state.app.tableHeader})
  }
}
</script>

<style scoped>

</style>



